*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  /* background: #222; */
  background: url('./Images/background.jpg') no-repeat;
  background-size: cover;
  background-position: center;
  color: #fff;
  font-family: "Quicksand", sans-serif;
  font-size: 18px;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  #root{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    .App{
      width: 100%;
      height: 100%;
      position: relative;
    }  
  }  
}



