@import url(https://fonts.googleapis.com/css?family=Dancing+Script);
*{margin:0;padding:0;box-sizing:border-box}body{background:url(/react-todolist/static/media/background.1ee55e5c.jpg) no-repeat;background-size:cover;background-position:center;color:#fff;font-family:"Quicksand", sans-serif;font-size:18px;height:100vh;width:100vw;overflow:hidden}body #root{width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}body #root .App{width:100%;height:100%;position:relative}

.Notebook{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;text-align:center;text-transform:uppercase;height:100%;width:100%;position:absolute;z-index:10}.Notebook .notebook-wrapper{max-width:calc(100% / 2);min-width:40em;max-height:calc 100%;min-height:100%}.Notebook .notebook-wrapper .notebook{height:40em;width:70%;position:relative;transition:0.4s ease-in-out;border-radius:5px 15px 15px 5px;-webkit-transform-origin:left center 0px;transform-origin:left center 0px;display:inline-block;margin:30px;-webkit-perspective:2000px;perspective:2000px;-webkit-transform:rotate(-5deg);transform:rotate(-5deg)}.Notebook .notebook-wrapper .notebook .notebook-cover{background:url(/react-todolist/static/media/leather-texture.a1f83314.png);background-size:contain;height:100%;width:100%;position:absolute;border-radius:5px 15px 15px 5px;z-index:100;transition:0.5s linear;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;-webkit-transform-origin:left center 0px;transform-origin:left center 0px;top:15%}.Notebook .notebook-wrapper .notebook .notebook-cover::before{content:"";position:absolute;width:15px;height:calc(100% + 2px);top:-1px;border-radius:2px;right:25px;transition:2s ease;background:linear-gradient(to right, #1e606e 0%, #2e95aa 12%, #1e606e 25%, #2e95aa 37%, #1e606e 50%, #2e95aa 62%, #1e606e 75%, #2e95aa 87%, #1e606e 100%)}.Notebook .notebook-wrapper .notebook .notebook-cover .notebook-skin{height:150px;background:#e8e8e0;margin-top:80px;padding:1em;font-size:2em;position:relative;z-index:-10;color:#222;text-align:left;box-shadow:0 1px 1px rgba(0,0,0,0.2);top:15%}.Notebook .notebook-wrapper .notebook .notebook-cover .notebook-skin::before{content:"© Dalibor König, 2021";padding-top:20px;padding-left:5px;font-size:small;position:absolute;width:99%;height:15px;left:0;bottom:0;background:#cddc39}.Notebook .notebook-wrapper .notebook .notebook-page{height:100%;width:100%;position:absolute;background-color:#fbfae8;z-index:0;border-radius:5px 16px 16px 5px;overflow:hidden;background:linear-gradient(to bottom, #fbfae8 19px, #d1d1d1 1px);background-size:100% 20px;top:15%}

.ToDoList{width:100%;height:100%;position:absolute;top:0;left:0;z-index:10}.ToDoList .mainTodo{position:absolute;top:0;left:0;width:100%;z-index:100;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:20px}.ToDoList .mainTodo .addItems{width:100%;display:-webkit-flex;display:flex;margin-bottom:20px}.ToDoList .mainTodo .addItems>input:nth-child(1){width:80%}.ToDoList .mainTodo .addItems>input:nth-child(2){cursor:pointer;width:20%}.ToDoList .mainTodo .editItems{width:100%;display:-webkit-flex;display:flex;margin-bottom:20px;position:absolute;left:0;top:0px;padding:20px;z-index:200}.ToDoList .mainTodo .editItems>input:nth-child(1){width:80%}.ToDoList .mainTodo .editItems>input:nth-child(2){cursor:pointer;width:20%}.ToDoList .mainTodo .task{display:-webkit-flex;display:flex;margin-bottom:-1px;-webkit-justify-content:space-between;justify-content:space-between}.ToDoList .mainTodo .task>input,.ToDoList .mainTodo .task input:active{width:80%;background-color:transparent;border:none;outline:none}.ToDoList .mainTodo .task .icons>.far{color:black;padding-left:8px;cursor:pointer}

